body, html {
  margin: 0;
  padding: 0;
  font-family: 'Inter', sans-serif !important;
  color: #3C4554;
  font-size: 14px;
  line-height: 20px;
}

*:focus {
  outline: none;
}

h1 {
  font-size: 32px;
  font-weight: 700;
  line-height: 45px;
  letter-spacing: -1.5;
  color: #424242;
}

h2 {
  font-size: 24px;
  font-weight: 700;
  line-height: 30px;
  letter-spacing: -0.5;
}

h3 {
  font-size: 22px;
  font-weight: 700;
  letter-spacing: -0.15;
  margin: 0;
}

h4 {
  font-size: 18px;
  line-height: 20px;
  margin-top: 0;
  letter-spacing: 0.15;
}

.subtitle { 
  color: #B5B5B5;
  font-size: 18px;
}

small {
    font-size: 12px;
}

a, a:link, a:visited {
    color: #555555;
}

a:hover {
    color: #888888;
}

p {
  color: #636363;
}

